/* --- –ó–∞–≥–∞–ª—å–Ω–∏–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä --- */
.home-container {
  position: relative;
  min-height: 100vh;
  font-family: "Orbitron", sans-serif;
  color: #f0f0f0;
  background: linear-gradient(180deg, #0c0c1a8a 0%, #0c0c1a8a 100%);
  overflow-x: hidden;
  padding: 20px 40px; /* –±—ñ–ª—å—à–µ –≤—ñ–¥—Å—Ç—É–ø—ñ–≤ –∑ –±–æ–∫—ñ–≤ */
}

/* --- Hero —Å–µ–∫—Ü—ñ—è --- */
.hero-section {
  max-width: 1300px;
  margin: 80px auto 60px; /* –±—ñ–ª—å—à–µ —Ä–æ–∑—Ä–∏–≤—ñ–≤ –∑–≤–µ—Ä—Ö—É —ñ –∑–Ω–∏–∑—É */
  padding: 6rem 1rem 3rem;
  text-align: center;
}

.hero-badge {
  display: inline-block;
  padding: 0.3rem 0.9rem;
  background: #3a3a4f;
  border-radius: 999px;
  font-size: 0.8rem;
  letter-spacing: 1px;
  margin-bottom: 1rem;
  color: #fff;
}

.hero-title {
  font-size: 3rem;
  font-weight: 800;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.hero-gradient-text {
  background: linear-gradient(90deg, #ff6ec7, #7c5fff, #00d4ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}

.hero-description {
  max-width: 750px;
  margin: 0 auto 2.5rem;
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.6;
}

/* --- Hero buttons --- */
.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.btn-primary {
  background: #1350b3;
  color: #fff;
  padding: 0.75rem 2rem;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  font-size: 1rem;
}

.btn-primary:hover {
  background: #7c5fff;
}

.btn-secondary {
  background: transparent;
  color: #fff;
  border: 2px solid #1350b3;
  padding: 0.7rem 2rem;
  border-radius: 50px;
  font-weight: 600;
  font-size: 1rem;
}

.btn-secondary:hover {
  background: rgba(124, 95, 255, 0.1);
}

/* --- –ó–∞–≥–æ–ª–æ–≤–∫–∏ —Å–µ–∫—Ü—ñ–π --- */
.cards-section {
  margin: 60px auto; /* –±—ñ–ª—å—à—ñ –≤—ñ–¥—Å—Ç—É–ø–∏ –º—ñ–∂ —Å–µ–∫—Ü—ñ—è–º–∏ */
  padding-left: 40px; /* —Ç—Ä–æ—Ö–∏ –ø—Ä–∞–≤—ñ—à–µ */
}

.cards-section h2 {
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #fff;
  opacity: 0.95;
  text-align: left;
}

/* --- –°—ñ—Ç–∫–∞ –∫–∞—Ä—Ç–æ–∫ --- */
.cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 1.5rem;
}

/* --- –ö–∞—Ä—Ç–∫–∏ --- */
.card {
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.15);
  border-radius: 16px;
  padding: 1.25rem 1.5rem;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  transition: all 0.3s ease;
}

.card:hover {
  border-color: #7c5fff;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(14px);
  -webkit-backdrop-filter: blur(14px);
}

.card-inner {
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.15);
  border-radius: 16px;
  padding: 1.25rem 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  transition: all 0.3s ease;
}

.card-inner:hover {
  border-color: #7c5fff;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(14px);
  -webkit-backdrop-filter: blur(14px);
}

.card-icon {
  margin-bottom: 0.5rem;
}

.card-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 12px;
  margin-bottom: 0.8rem;
}

.card-title {
  font-weight: 700;
  font-size: 1.05rem;
}

.card-desc {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.75);
  line-height: 1.4;
}

/* --- –ú–æ–¥–∞–ª–∫–∞ --- */
.login-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.login-modal {
  background: #1e1e1e;
  padding: 25px 20px; /* —Ç—Ä–æ—Ö–∏ –º–µ–Ω—à–µ padding –∑ –±–æ–∫—ñ–≤ */
  border-radius: 12px;
  text-align: center;
  max-width: 400px;
  width: 90%;
  color: white;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.4);
  position: relative;
}

.login-modal p {
  margin-bottom: 20px;
  font-size: 16px;
}

/* –°—Ç–∏–ª—ñ–∑–æ–≤–∞–Ω–∞ –∫–Ω–æ–ø–∫–∞ –∑–∞–∫—Ä–∏—Ç—Ç—è */
.login-modal-close-btn {
  position: absolute;
  top: 8px;
  right: 10px;
  background: #333;
  border: none;
  color: white;
  font-size: 16px;
  height: 32px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s ease;
}

.login-modal-close-btn:hover {
  background-color: #555;
}

/* --- –ú–µ–¥—ñ–∞ –∑–∞–ø–∏—Ç–∏ --- */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2rem;
  }
  .hero-description {
    font-size: 0.95rem;
  }
  .btn-primary,
  .btn-secondary {
    width: 100%;
    padding: 0.75rem 0;
  }
  .cards-grid {
    grid-template-columns: 1fr;
  }
  .cards-section {
    padding-left: 20px;
    margin: 40px auto;
  }
}
